<template>
    <div id="app">
      <h2>Tugas: membuat aplikasi kalkulator sederhana menggunakan Vue.js</h2>
      <div class="calculator">
        <div class="input-container">
          <input v-model="angka1" type="number">
          <input v-model="angka2" type="number">
        </div>
        <div class="button-container">
          <button @click="tambah">+</button>
          <button @click="kurang">-</button>
          <button @click="kali">*</button>
          <button @click="bagi">/</button>
        </div>
        <div class="result-container">
          <p>Hasil: {{ hasil }}</p>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        angka1: 0,
        angka2: 0,
        hasil: 0,
      };
    },
    methods: {
      tambah() {
        this.hasil = Number(this.angka1) + Number(this.angka2);
      },
      kurang() {
        this.hasil = Number(this.angka1) - Number(this.angka2);
      },
      kali() {
        this.hasil = Number(this.angka1) * Number(this.angka2);
      },
      bagi() {
        this.hasil = Number(this.angka1) / Number(this.angka2);
      },
    },
  };
  </script>
  
  <style scoped>
  .calculator {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .input-container {
    margin-bottom: 10px;
  }
  
  .button-container button {
    margin-right: 5px;
  }
  
  .result-container {
    margin-top: 10px;
  }
  </style>